// -----------------------------------------------------------------------------

.category-box  {

	.block-img {
		width: 30%;
		box-sizing: border-box;
    	padding: 0.3em;

    	img {
    		border-radius: 0.5em;
    		box-shadow: 2px 2px 1px #D7D7D7;
    	}
	}

	.block-summary {
	    width: 68%;
	    margin-left: 2%;
	    box-sizing: border-box;

	    .title {
	    	margin-top: 1em;
	        font-size: 1.2em;
		    font-weight: bold;
		    margin: 0;
		    /* color: #666; */
		    letter-spacing: 1px
	   	}    
	}



}

